header {
  background-color: #333;
  color: #fff;
  padding: 1rem 0;
  text-align: center;
}

header h1 {
  margin: 0;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 1rem;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

#BasicBoard {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  place-items: center; /* Center horizontally and vertically */
  width: 60%; /* Set a maximum width for the chessboard */
  margin: 0 auto; /* Center the chessboard */
  padding: 3rem;
}
